@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body {
  margin: 0 auto;
  font-family: "Work Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
  max-width: 80rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}





.Pokecard {
    border: 4px solid rgba(255, 192, 203, 0.308);
    width: 200px;
    border-radius: 4%;
    margin: 10px 5px;
    padding: 10px;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,.56);
    background-color: white;
}


.Pokecard-data {
    font-size: 80%;
}

.Pokecard-data > span {
    font-weight: bold;
}

.Pokecard-title {
    font-size: 1.5rem;
}

.Pokecard-img {
    height: 200px;
    width: auto;
    display: flex;
    justify-content: center;
    background-color: #eceff1;
    border-radius: 50%;
}

.Pokecard img {
    height: 150px;
    transition: all ease 500ms;
    align-self: center;
}

.Pokecard img:hover {
    transform: scale(1.25);
    opacity: 0.7
}


.Pokedex {
    text-align: center;
}
.Pokedex-cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;   

}

.Pokedex-winner {
    color: #4caf50;
}

.Pokedex-loser {
    color: #E91E63
}
